import React from 'react'; import styled from 'styled-components';
import NeonLogo from '~Client/Logo/NeonLogo';
// Imported svg 
import { ReactComponent as Navigator } from '~PREF/Img/MainNav/2.svg'; 
const NavImage = styled.div `display: flex; align-items: center; background: rgba(255,255,255,0.5); justify-content: center; height: 26px; width: 40px; border-radius: 20px;`
// Router 
const ComponentLink = styled(Link) `padding: 4px 5vh; color: #000; text-decoration: none; font-size: 100%; @media (max-width: 500px) { padding: 2px 1vh; font-size: 80%;}`
import { Link } from "react-router-dom"; 
// Router components
export default class MainNav extends React.Component {
render () { return (
    <Main$Nav$Wrapper>
        <nav><SectionNav>
            <li><NavigationHeader> 
                <li><NavImage> <Navigator /> </NavImage></li> {/* + */} <li> <About>Navigation</About> </li> 
            </NavigationHeader></li>
            <li style={{width: '100%', height: '100%'}}><Navigation>
                <Item><ComponentLink to="/">Main</ComponentLink></Item>
                <Item><ComponentLink to="/cyberial">About me</ComponentLink></Item>
                <Item><ComponentLink to="/code">code</ComponentLink></Item>
            </Navigation></li>
            <li><NeonLogo logo='Cyberial'/></li>
        </SectionNav></nav>
    </Main$Nav$Wrapper>
)}}

const Main$Nav$Wrapper = styled.div `height: 5vh; z-index: 22; position: relative; margin-top: 2px; width: 100%; box-shadow: 0 0 4px 0 #fff,inset 0 0 14px 0 #000; border-radius: 20px;
    @media (max-width: 800px){ padding-right: 0%; }` 
const SectionNav       = styled.ul  `perspective: 500px; height: 100%; width: 100%; display: flex; align-items: center; 
     font-weight: 600; letter-spacing: 0,8px; text-transform: uppercase; white-space: nowrap;`
const NavigationHeader = styled.ul  `display: flex; left: 6px; border-radius: 0 10px 10px; align-items: center; position: relative; width: 100%;`
const About            = styled.h2  `padding: 0px 5px; display: block; left: 32px; font-family: 'Architects Daughter', cursive; color: #fff; font-size: 90%; 
    @media (max-width: 700px){ display: none; background: none }`
// --------------------------------------------/------------------------------------------------------------
const Navigation       = styled.ul `display: flex; width: 100%; justify-content: space-evenly; align-items: center;`
const Item             = styled.li `max-height: 24px;  background: #fff; margin: 0px 2px; display: flex; justify-content: center; align-items: center;
    transition: background, transform 1s; border-radius: 20px 4px 20px 4px; text-align: center; font-family: 'Architects Daughter', cursive;;
        &:hover { background: rgba(58, 155, 206, 0.2); }`
