import React from 'react'; import styled from 'styled-components'; 
// SVG there
import { ReactComponent as VK } from '~PREF/img/Social/vk.svg'; import { ReactComponent as FACEBOOK } from '~PREF/img/Social/facebook.svg';
import { ReactComponent as INSTA } from '~PREF/img/Social/instagram.svg'; 

export default class SocialContact extends React.Component {
    render () { return (
        <>
            <Item><a href="https://vk.com/gooshan4uk"><VK></VK></a></Item>
            <Item><a href='https://www.facebook.com/cyberial.syntwaiser'><FACEBOOK></FACEBOOK></a></Item>
            <Item><a href='https://www.instagram.com/cyber_area/'><INSTA></INSTA></a></Item>
        </>

    )}}
const Item       = styled.li `z-index: 1111; width: 28px; height: 28px; border-radius: 100%; 
    box-shadow: 0 0 6px 0px #000, 0 0 30px 0px rgba(198,57,57,1)`