import React from 'react';  import styled from 'styled-components';

export default class StatusReaderContainer extends React.Component {
    render () { return (
            <AppContainer className="AppContainer">
                {this.props.children}
            </AppContainer>
    )}
}

const AppContainer = styled.div `     {/* Main component constructor for App */}
    width: 100%; height: 100%; position: relative; display: flex; flex-direction: column; 
    &:after { content: ''; position: absolute; width: 100%; 
    box-shadow: inset 0px 0px 100px #2e004d, inset 0px 0px 100px rgba(92,19,73,1); }
} `
