import React from 'react'; import styled from 'styled-components'; 

const Content = (props) => (
    <ContentContainer className='Content'>
        {props.children}
    </ContentContainer>
)

const ContentContainer = styled.div `
    width: 98%; min-height: 93%; margin: 4px auto; overflow: auto; 
    border-radius: 60px 125px 60px 60px; font-family: 'Baloo 2', cursive; background: #fff; z-index: 1; 
    box-shadow: 0px 0px 5px 0px #FEC; 
    `




export default Content