import React from 'react';       import styled from 'styled-components';     import { CSSTransition } from "react-transition-group";
import SocialContact from '../SocialContact/SocialContact'; import '../SocialContact/SocialAnimated.scss';

export default class NeonLogo extends React.Component {
  constructor(props){
    super(props);
    this.switch = this.switch.bind(this)

    this.state = {
      showList: false,
    }
  }

  switch() {
    this.setState(prevState => ({
      showList: !prevState.showList
    }));
  };
    render () { return (
            <Logo onClick={this.switch}>
                <LogoSlogan>{this.props.logo}</LogoSlogan>
                <CSSTransition in={this.state.showList} timeout={800} classNames="list-transition" 
                        unmountOnExit appear onEntered={this.listSwitch} onExit={this.listSwitch}>
                  <Links><SocialContact/></Links>
                </CSSTransition>
            </Logo>
        )
    }
} 
const Logo     = styled.div `max-width: 80px; margin-right: 6px; padding: 4px 6px; height: 26px; cursor: pointer; transform: rotate(0deg); 
display: flex; justify-content: center; align-items: center; position: relative; 
  border-radius: 25%; z-index: 999; animation: neon 2s ease-in-out infinite alternate; background: #222;
  box-shadow: 0 0 4px 1px #a82424;  
        @keyframes neon {                     ${'' /* Neon RGBA colors set for main App */}
          from {
            text-shadow:
              0 0 33px 1px rgb(230, 0, 0),            0 0 30px 1px rgb(255, 0, 255),            0 0 15px 1px rgba(198, 57, 57, 1),
            }
          to {
            text-shadow:
              0 0 33px 3px rgba(198, 57, 57, 1),      0 0 30px rgb(255, 255, 255),          0 0 33px rgba(198, 57, 57, 1),
            }}}`; 
const LogoSlogan = styled.h1 `font-size: 85%; font-family: 'Architects Daughter', cursive; color: pink; margin: 0; font-weight: 400;`;
const Links = styled.ul `display: flex; justify-content: space-between; position: absolute; width: 140px; height: 24px; `




