import React, { Suspense, lazy } from 'react';
import { Switch, Route } from 'react-router-dom';
// ------------------------------------------------------------------------------
const Main      = lazy(() => import('~Client/Containers/Content/Main/Main')); 
const AboutMe   = lazy(() => import('~Client/Containers/Content/AboutMe/AboutMe'));
const Code      = lazy(() => import('~Client/Containers/Content/AboutMe/Code'));
// ------------------------------------------------------------------------------
const Routes = (props) => {
    return (
        <>
            <Suspense fallback={<div style={{position: "absolute", color: "#fff", right: "35%", top: "35%", fontSize: "40px", textTransform: "uppercase"}}>Loading - Please Wait.</div>}>
                <Switch>
                    <Route      exact path="/"    component={ (() => ( <Main /> ))}     />
                    <Route      path="/cyberial"  component={ (() => ( <AboutMe /> ))}  />
                    <Route      path="/code"      component={ (() => ( <Code /> ))}     />
                </Switch>
            </Suspense>
        </>
    )
}


export default Routes