// ...
import React from 'react'; import styled from 'styled-components'; 
// Gear SVG import 
import { ReactComponent as Control } from '~PREF/img/Control.svg';
const Setting = (props) => {
        return (
                <ControlContainer>
                    <Control/>
                </ControlContainer>
            )
    }


const ControlContainer = styled.div `position: fixed; bottom: 1%; right: 1%; z-index: 1111;
    &:before {  
        content: ''; box-shadow: 0 0 25px 15px #14141f, 0 0 0px 0px #9BF, 0 0 0px 0px #00bfff; 
        position: absolute; top: 50%; left: 50%; z-index: -1; }`



export default Setting