import React from 'react'; import styled from 'styled-components';  

const ButCloseHide = (props) => {
    return (
        <>
            <CloseHideButton>
                    <Hide></Hide>
                    <Close />
            </CloseHideButton>
        </>
    )
}


const CloseHideButton = styled.div `height: 50px; width: 90px; position: fixed; right: -0.6%; top: 7%; z-index: 1; display: flex; 
    background: none; border-radius: 100%; transform: rotate(44deg); `
    // ---------------------------------------------------------------->
const Hide            = styled.div ` border-radius: 100px 0px 0px 0px; background: #fff; width: 50%; height: 100%; border-top: 1.5px solid blue; border-right: 1px solid #000;
    &:before { content: ''; width: 12px;  height: 2.5px; transform: rotate(170deg); left: 22px; top: 14px; display: block; position: absolute; border-radius: 100%; background: #1f1f1e; }`
// ==========================================================================>
const Close           = styled.div ` border-radius: 0px 100px 0px 0px; border-left: 1px; position: absolute; right: 0; border-top: 1.5px solid red; margin: 0; background: #fff; width: 50%; height: 100%;
    &:before { content: ''; width: 12px;  height: 2.5px; transform: rotate(0deg); left: 9px; top: 13px; display: block; position: absolute; border-radius: 100%; background: #1f1f1e; }
    &:after  { content: ''; width: 2.5px; height: 12px;  transform: rotate(0deg); left: 14px; top: 8px; display: block; position: absolute; border-radius: 100%; background: #1f1f1e; }`





export default ButCloseHide